import { NgModule, LOCALE_ID } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { RouteReuseStrategy } from '@angular/router';
import { HttpClient, HttpClientModule } from '@angular/common/http';

import { IonicModule, IonicRouteStrategy } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';
import { ComponentsModule } from './components/components.module';

import {TranslateModule, TranslateLoader } from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import { AfrichomeDataProvider } from 'src/providers/africhome-data/africhome-data.provider';
import { AfrichomeControlAuthenticationProvider } from 'src/providers/africhome-control-fields/africhome-control-authentication.provider';
import { AfrichomeControlRegistrationProvider } from 'src/providers/africhome-control-fields/africhome-control-registration.provider';
import { AfrichomeObservablesProvider } from 'src/providers/africhome-observables/africhome-observables.provider';
import { AfrichomeFilesManagerProvider } from 'src/providers/africhome-files-manager/africhome-files-manager.provider';
import { AfrichomeControlAdProvider } from 'src/providers/africhome-control-fields/africhome-control-ad.provider';
import { AfrichomeTranslateProvider } from 'src/providers/africhome-translate/africhome-translate.provider';
import { AfrichomeConfigProvider } from 'src/providers/africhome-config/africhome-config.provider';


/**
 * Factory du loader http des fichiers de tradction
 * @param {HttpClient} httpClient client http angular 
 */
export function HttpLoaderFactory(httpClient: HttpClient) {
  return new TranslateHttpLoader(httpClient, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [AppComponent],
  entryComponents: [AppComponent],
  imports: [
    IonicModule,
    AppRoutingModule, 
    ComponentsModule,
    BrowserModule, 
    HttpClientModule,
    IonicModule.forRoot(),
    TranslateModule.forRoot({
      loader: {
          provide: TranslateLoader,
          useFactory: HttpLoaderFactory,
          deps: [HttpClient]
      }
    })
  ],
  providers: [
    StatusBar,
    SplashScreen,
    AfrichomeControlAuthenticationProvider,
    AfrichomeFilesManagerProvider,
    AfrichomeControlRegistrationProvider,
    AfrichomeControlAdProvider,
    AfrichomeDataProvider,
    AfrichomeTranslateProvider,
    AfrichomeObservablesProvider,
    AfrichomeConfigProvider,
    { provide: RouteReuseStrategy, useClass: IonicRouteStrategy }
  ],
  bootstrap: [AppComponent]
})
export class AppModule {}
