import { Component, OnInit } from '@angular/core';
import { AfrichomeDataProvider } from 'src/providers/africhome-data/africhome-data.provider';
import { AfrichomeObservablesProvider } from 'src/providers/africhome-observables/africhome-observables.provider';
import { TranslateService } from '@ngx-translate/core';
import { AfrichomeControlRegistrationProvider } from 'src/providers/africhome-control-fields/africhome-control-registration.provider';

@Component({
  selector: 'africhome-professional-registration',
  templateUrl: './africhome-professional-registration.component.html',
  styleUrls: ['./africhome-professional-registration.component.scss', '../registration-theme.scss'],
})
export class AfrichomeProfessionalRegistrationComponent implements OnInit {

  /**
   * Nom de l'utilisateur
   */
  public lastName: string;

  /**
   * Prénom de l'utilisateur
   */
  public firstName: string;

  /**
   * Nom de la société
   */
  public societyName: string;

  /**
   * SIRET de la société
   */
  public SIRETNumber: string;

  /**
   * Adresse de la société
   */
  public adress: string;

  /**
   * Ville ou se trouve la société
   */
  public city: string;

  /**
   * 
   * Pays ou se trouve la société
   */
  public country: string;

  /**
   * Numéro de téléphone
   */
  public number: string;

  /**
   * Email
   */
  public email: string;

  /**
   * Mot de passe
   */
  public password: string;

  /**
   * Confirmation mot de passe
   */
  public confirmPassword: string;

  /**
   * Liste des pays
   */
  public countries: string[];

  /**
   * Afficher ou non le spinner
   */
  public hideSpinner = true;

  /**
   * Messge d'erreur
   */
  public errorMsg: string;

  /**
   * Placeholder du mot de passe
   */
  public passwordPlaceholder: string;

  constructor(private dataProvider: AfrichomeDataProvider,
              private translateService: TranslateService,
              private observableProvider: AfrichomeObservablesProvider,
              private controlFields: AfrichomeControlRegistrationProvider) {
      this.getMessage('INFO_BULLE.REGISTRATION.INFO.PASSWORD', 'passwordPlaceholder');
   }

  ngOnInit() {
      this.countries = this.dataProvider.getCountries();
      // On ecoute l'observable et dès que la langue change, on change le placeholder du MDP
      this.observableProvider.translateEmiter.subscribe(
        (language) => {
          if (language === 'fr') {
            this.passwordPlaceholder = 'Au moins 6 caractères';
          } else {
            this.passwordPlaceholder = 'At least 6 characters';
          }
        }
      );
  }

  public registration() {
     // Test si les champs sont existants
     if (this.lastName && this.firstName && this.societyName &&
        this.SIRETNumber && this.adress && this.city && this.country &&
        this.number && this.password && this.email && this.confirmPassword) {

      const fieldsValues = {
        lastName: this.lastName,
        firstName: this.firstName,
        societyName: this.societyName,
        SIRET: this.SIRETNumber,
        adress: this.adress,
        city: this.city,
        country: this.country,
        number: this.number,
        email: this.email,
        password: this.password,
        confirmPassword: this.confirmPassword
      }

      const result = this.controlFields.registration(fieldsValues, 'professional');

      this.hideSpinner = result.hideSpinner;
      if (result.message != null) {
        this.getMessage(result.message, 'errorMsg');
      } else {
        this.errorMsg = result.message;
        console.log(' Faire la réquete pour inscrire la société ');
      }

    } else {
        this.getMessage('INFO_BULLE.EMPTY_FIELD', 'errorMsg');
      }
  }

  /**
   * Stocke le pays dans l'attribut country
   * @param country Pays choisi
   */
  public setCountry(country: string) {
    this.country = country;
  }

  /**
  * Renvoie le bon message
  * @param message message à afficher
  * @param attribut attribut à utiliser pour stocker le message
  */
  private getMessage(message: string, attribut: string) {
      this.translateService.get(message).subscribe((res: string) => {
          if (attribut === 'errorMsg') {
            this.errorMsg = res;
          } else {
            this.passwordPlaceholder = res;
          }
      });
  }
}
