import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'africhome-button',
  templateUrl: './africhome-button.component.html',
  styleUrls: ['./africhome-button.component.scss'],
})
export class AfrichomeButtonComponent implements OnInit {
  
  /**
   * theme du bouton
   */
  @Input() theme: string;

  /**
   * titre du bouton
   */
  @Input() title: string;

  /**
   * Evenement émis lors du click du bouton
   */
  @Output() clickButton = new EventEmitter<string>();

  constructor() { }

  ngOnInit() {}

}
