import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { AfrichomeFilesManagerProvider } from 'src/providers/africhome-files-manager/africhome-files-manager.provider';
import { AfrichomeTranslateProvider } from 'src/providers/africhome-translate/africhome-translate.provider';
import { AfrichomeObservablesProvider } from 'src/providers/africhome-observables/africhome-observables.provider';

@Component({
  selector: 'africhome-input',
  templateUrl: './africhome-input.component.html',
  styleUrls: ['./africhome-input.component.scss', '../africhome-registration/registration-theme.scss']
})
export class AfrichomeInputComponent implements OnInit {

  /**
   * Label de l'input
   */
  @Input() public label: string;

  /**
   * Type de l'input
   */
  @Input() public type: string;

  /**
   * Valeur de l'input
   */
  public value: any;

  /**
   * Afficher l'erreur ou non
   */
  public displayError = false;

  /**
   * Placeholder
   */
  @Input() public placeholder: string;

  /**
   * Liste des options
   */
  @Input() public selectValues: string[];

  /**
   * Liste des radios
   */
  @Input() public radioValues: string[];

  /**
   * Message d'erreur
   */
  @Input() public errorMsg: string;

  /**
   * Afficher l'étoile
   */
  @Input() public display = false;

  /**
   * Icon d'image
   */
  @Input() public icon: string;

  /**
   * la valeur de la ckeckbox
   */
  @Input() public checkboxValue: string;

  /**
   * La valeur de l'attribut name - radio
   */
  @Input() public nameAttributRadio: string;

  /**
   * Le type d'alignement à éffectuer : row, column
   */
  @Input() public direction: string;

  /**
   * Url de l'image uploadé
   */
  public imageBlobUrl = {
    image1: null,
    image2: null,
    image3: null
  };

  /**
   * Test si une image à été uploadé
   */
  public existImages = {
      image1: false,
      image2: false,
      image3: false
  };

  /**
   * Champ corespondant aux combinaisons : input - select
   */
  public inputSelect: {
    inputValue: any,
    selectValue: any
  };

  /**
   * Test si le checkbox à été cliqué
   */
  public clickCheckbox: boolean;

  /**
   * Stocke la clé pour traduire le placeholder
   */
  private placeholderTranslate: string;

  /**
   * Evenement emit lorsque le champ n'est plus ciblé
   */
  @Output() public emitValue = new EventEmitter<any>();


  constructor(private translateService: TranslateService,
              private managerFiles: AfrichomeFilesManagerProvider,
              private translate: AfrichomeTranslateProvider,
              private observable: AfrichomeObservablesProvider) {}

  ngOnInit() {

    // initialisation
    this.inputSelect = {
      inputValue: '',
      selectValue: ''
    };
    this.clickCheckbox = false;

    // translate placeholder
    if (this.placeholder) {
      this.placeholderTranslate = this.placeholder;
      this.placeholder = this.translate.getMessageInstant(this.placeholder);
    }
    this.observable.translateEmiter.subscribe(
      (_) => {
        if (this.placeholder) {
          this.placeholder = this.translate.getMessageInstant(this.placeholderTranslate);
        }
      }
    );

  }

  /**
   * Emet la valeur du champ
   */
  public emit() {
    this.emitValue.emit(this.value);
  }

  /**
   * Emet la valeur du radio
   */
  public emitRadioChoice(value: string) {
    this.value = value;
    this.emit();
  }

  /**
   * Emet la select
   * @param value valeur
   */
  public getSelect(value: string) {
    this.translate.getMessage(value).subscribe(
      (trans) => {
        this.value = trans;
        this.emit();
      }
    );
  }

  /**
   * Renvoie le bon message
   * @param message message à afficher
   * @param attribut attribut à utiliser pour stocker le message
   */
  private getMessage(message: string, attribut: string) {
    this.translateService.get(message).subscribe((res: string) => {
        if (attribut === 'errorMsg') {
          this.errorMsg = res;
          this.displayError = true;
        }
        if (attribut === 'placeholder') {
          this.placeholder = res;
        }
        if (attribut === 'label') {
          this.label = res;
        }
    });
  }

  /**
  * Renvoie le bon message
  * @param array tableau contenant les messages
  */
  private getArrayMessage(array: string[]) {
    let i;
    for (i = 0; i < array.length; i++) {
      this.translateService.get(array[i]).subscribe((res: string) => {
        array[i] = res;
      });
    }
  }

  /**
   * Crée une image et l'affiche
   * @param image image
   * @param posititon numéro de l'image => image1, image2, image3
   */
  public createImageFromBlob(image: Blob, posititon: string) {
    this.managerFiles.createImageFromBlob(image).subscribe(
      (url) => {
        this.imageBlobUrl[posititon] = url;
        this.existImages[posititon] = true;
        this.emitValue.emit(this.imageBlobUrl);
      }
    );
  }

  /**
   * Supprimer l'image
   * @param posititon numéro de l'image
   */
  public removeImage(posititon) {
    this.imageBlobUrl[posititon] = null;
    this.existImages[posititon] = false;
    this.emitValue.emit(this.imageBlobUrl);
  }

  /**
   * Verifie si la valeur du champ est valide sinon renvoie une erreur
   */
  public handle() {
    if (this.value !== undefined && this.value.trim() !== '') {
      this.displayError = false;
      this.emit();
    } else {
      // tslint:disable-next-line: no-unused-expression
      this.errorMsg !== 'not-required' ? this.getMessage(this.errorMsg, 'errorMsg') : '';
    }
  }

  /**
   * Verifie la valeur du textarea
   * @param event evenement emis
   */
  public handleTextarea(event: any) {
    this.value =  event.target.value;
    this.handle();
  }

  /**
   * Verifie la valeur de l'input et du select
   * @param field champ
   * @param selectValue valeur du select
   */
  public handleInputSelect(field: string, selectValue?: string){
    if (field === 'inputValue') {
      this.inputSelect.inputValue = this.value;
      this.emitInputSelect();
    } else {
      this.translate.getMessage(selectValue).subscribe(
        (trans) => {
            this.inputSelect.selectValue = trans;
            this.emitInputSelect();
        }
      );
    }
  }

  /**
   * Emet la valeur du checkbox
   * @param event évenement émis
   */
  public handleCheckbox(event: any) {
    this.clickCheckbox = !this.clickCheckbox;
    this.value = this.clickCheckbox;
    this.emit();
  }
  /**
   * Emet les valeurs du champ de type input-select
   */
  private emitInputSelect() {
    if (this.inputSelect.inputValue !== '' && this.inputSelect.selectValue !== '') {
      this.displayError = false;
      this.emitValue.emit(this.inputSelect);
    } else {
      // tslint:disable-next-line: no-unused-expression
      this.errorMsg !== 'not-required' ? this.getMessage(this.errorMsg, 'errorMsg') : '';
    }
  }
}
