import { Component, OnInit } from '@angular/core';
import { AfrichomeDataProvider } from 'src/providers/africhome-data/africhome-data.provider';
import { ModalController } from '@ionic/angular';

@Component({
  selector: 'africhome-search',
  templateUrl: './africhome-search.component.html',
  styleUrls: ['./africhome-search.component.scss'],
})
export class AfrichomeSearchComponent implements OnInit {
  /**
   * Les valeurs des champs
   */
  private fields = {
    search : {
      value: ''
    },
    location: {
      country: '',
    },
    adType: {
      rent: false,
      buy: false
    },
    propertyType: {
      value: '',
      numberRooms: 0
    },
    surface: {
      value: ''
    },
    price: {
      value: '',
      currency: ''
    }
  };

  /**
   * Type de bien
   */
  public propertyType: any[];

  /**
   * Nombre de pièces
   */
  public numberRooms: any[];

  /**
   * Devise du prix
   */
  public devisePrice: any[];

  /**
   * Liste des pays
   */
  public countries: any[];


  constructor(private dataProvider: AfrichomeDataProvider,
              private modalController: ModalController) { }

  ngOnInit() {

    // type de biens
    this.propertyType = [];
    this.propertyType.push('COMPONENTS.SEARCH_AD.PROPERTY_TYPE.CHOOSE')
    this.propertyType.push('COMPONENTS.SEARCH_AD.PROPERTY_TYPE.HOUSE');
    this.propertyType.push('COMPONENTS.SEARCH_AD.PROPERTY_TYPE.APPART');
    this.propertyType.push('COMPONENTS.SEARCH_AD.PROPERTY_TYPE.LAND');
    this.propertyType.push('COMPONENTS.SEARCH_AD.PROPERTY_TYPE.OFFICE_SHOP');

    // nombre de pièces
    this.numberRooms = [];
    this.numberRooms.push('COMPONENTS.PUBLISH_AD.YOUR_AD.NUMBER_OF_ROOMS.CHOOSE');
    this.numberRooms.push('1');
    this.numberRooms.push('2');
    this.numberRooms.push('3');
    this.numberRooms.push('4');
    this.numberRooms.push('5+');

    // type de monnaie
    // TODO : compléter
    this.devisePrice = [];
    this.devisePrice.push('COMPONENTS.SEARCH_AD.PRICE.CURRENCY');
    this.devisePrice.push('CFA');
    this.devisePrice.push('FGN');

    // liste des pays
    // TODO: traduire en anglais
    this.countries = this.dataProvider.getCountries();
  }

  /**
   * Recupère les valeurs des champs
   * @param value valeur du champ
   * @param field1 champ 1
   * @param field2 champ 2
   */
  public getChoice(value: any, field1: string, field2: string) {
    if (field2 === '') {
      this.handleInputSelect(value, field1);
    } else {
      this.fields[field1][field2] = value;
    }
  }

  /**
   * Recupère la valeur du champ de type inputSelect
   * @param value valeur du champ
   * @param field1 champ
   */
  private handleInputSelect(value: any, field1: string) {
    this.fields[field1].value = value.inputValue;
    this.fields[field1].currency = value.selectValue;
  }

  /**
   * Traitement du formulaire
   * TODO: Verifiez pour les select que la valeur par défaut n'est pas celle renvoyer 
   * (ex:choisissez un bien)
   */
  public handleForm() {
    
    console.log(this.fields);
  }

  /**
   * Ferme le modal
   */
  public close() {
    this.modalController.dismiss({
      dismissed: true
    });
  }
}
