import { Component, OnInit, Input, Output } from '@angular/core';
import { AfrichomeConfigProvider } from 'src/providers/africhome-config/africhome-config.provider';

@Component({
  selector: 'africhome-ads',
  templateUrl: './africhome-ads.component.html',
  styleUrls: ['./africhome-ads.component.scss'],
})
export class AfrichomeAdsComponent implements OnInit {

  /**
   * Liste des annonces
   */
  @Input() public ads: any[];

  /**
   * Les valeurs pour trier
   */
  @Input() public sortValues: any[];

  /**
   * La localisation
   */
  @Input() public localisation: string;

  /**
   * Test si la platform est de type web
   */
  public isWeb = false;

  /**
   * Liste des annonces temporaire
   */
  public adsTmp = [];

  /**
   * La limite pour récuperer les données
   */
  public limit = 0;

  constructor(private config: AfrichomeConfigProvider) { }

  ngOnInit() {
    // TMP : supprimer
    this.localisation = 'Sénégal - Dakar';
    this.adsTmp = this.ads.slice(this.limit, this.limit + 10);
    this.limit += 10;
    this.isWeb = this.config.isWeb();
  }

  /**
   * Traite la partie des filtres
   * @param choice choix des filtres
   */
  public handle(choice: string){
    console.log(choice)
  }

  /**
   * Charge les données
   * @param event evenement emis lors du scroll
   */
  public loadData(event: any) {
    setTimeout(() => {
      event.target.complete();
      this.moreData();
      // App logic to determine if all data is loaded
      // and disable the infinite scroll
      if (this.adsTmp.length === this.ads.length) {
        event.target.disabled = true;
      }
    }, 1000);
  }

  /**
   * Ajouter plus de données (10 données)
   */
  private moreData() {
    const newArray = this.ads.slice(this.limit, this.limit + 10);
    this.adsTmp = this.adsTmp.concat(newArray);
    this.limit += 10;
  }

}
