import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { AfrichomeTranslateProvider } from 'src/providers/africhome-translate/africhome-translate.provider';

@Component({
  selector: 'africhome-radio-group',
  templateUrl: './africhome-radio-group.component.html',
  styleUrls: ['./africhome-radio-group.component.scss'],
})
export class AfrichomeRadioGroupComponent implements OnInit {

  /**
   * Liste des options
   */
  @Input() public radioValues: string[];

  /**
   * La valeur de l'attribut name
   */
  @Input() public nameAttribut: string;

  /**
   * Evenement emit
   */
  @Output() public emitValue = new EventEmitter<string>();

  constructor(private translateService: AfrichomeTranslateProvider) { }

  ngOnInit() {
  }


  /**
   * Emet la valeur du choix
   */
  public emit() {
    const query  = 'input[name=' + this.nameAttribut + ']:checked';
    const value = document.querySelector(query).getAttribute('value');
    this.translateService.getMessage(value).subscribe(
      (trans) => {
        this.emitValue.emit(trans);
      }
    );
  }

}
