import { Component } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Platform, MenuController } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { AfrichomeConfigProvider } from 'src/providers/africhome-config/africhome-config.provider';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})
export class AppComponent {

  public stateMenu = false;

  /**
   * Test si l'utilisateur est connecté ou non (connected, disconnected)
   */
  private status = 'disconnected';
  
  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    private translate: TranslateService,
    private menu: MenuController,
    private config: AfrichomeConfigProvider
  ) {
    this.initializeApp();
  }

  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();
      this.configTranslate();
      this.setPlatform();
    });
  }

  public setPlatform() {
    const platforms = this.platform.platforms();
    console.log(platforms[0]);
    this.config.setPlatform(platforms[0]);
}

  /**
   * Gère l'ouverture du menu
   */
  public menuOpening() {

    this.menu.isOpen().then((state: boolean) => {

      if (state) {
        this.menu.close();
      } else {
        this.menu.open();
      }
    });
  }

  /**
   * Configurer le service de traduction
   */
  public configTranslate(){
    this.translate.addLangs(['fr', 'en']);
    this.translate.setDefaultLang('fr');
  }

  /**
   * Pour changer l'etat du menu et appliquer le transparent gaussien sur l'ensemble de page
   * @param state l'etat du menu
   */
  public menuChanging(state: boolean) {
    this.stateMenu = state;
  }

  
  // private initTranslate(): Observable<any> {


  // }
}
