import { Component, OnInit, Input } from '@angular/core';
import { MenuController, NavController } from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import { AfrichomeObservablesProvider } from 'src/providers/africhome-observables/africhome-observables.provider';

@Component({
  selector: 'africhome-menu',
  templateUrl: './africhome-menu.component.html',
  styleUrls: ['./africhome-menu.component.scss'],
})
/**
 * Menu latéral de l'application
 */
export class AfrichomeMenuComponent implements OnInit {

  /**
   * Etat du menu
   * true = ouvert
   */
  @Input() isOpen: boolean;

  @Input() status: string = 'disconnected';

  /**
   * Constructeur
   * @param menu: récupération du menu
   */
  constructor(
    private menu: MenuController,
    private navCtrl: NavController,
    private translate: TranslateService,
    private observableProvider: AfrichomeObservablesProvider) {}

  ngOnInit() { }

  /**
   * Ferme le menu lors du clique sur le bouton
   */
  public closeMenu() {
    this.menu.close();
  }

  /**
   * Permet d'être redirigé sur la page de la fonctionnalité sélectionné
   * @param page : string
   */
  public goToPage(page: string) {
    this.navCtrl.navigateForward(page);
    this.menu.close();
  }

  /**
   * Déconnecte l'utilisateur et le rédirige vers la page d'accueil
   */
  public disconnect() {
    this.navCtrl.navigateRoot('');
    this.menu.close();
  }
  
  /**
   * Rédirige l'utilisateur vers la page de connexion
   */
  public connect() {
    this.navCtrl.navigateForward('authentication');
    this.menu.close();
  }

  /**
   * Permet de changer la langue
   * @param language langue courant
   */
  public changeLanguage(language: string) {
    this.translate.use(language);
    this.observableProvider.translateEmiter.emit(language);
  }
}
