import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'africhome-personal-informations',
  templateUrl: './africhome-personal-informations.component.html',
  styleUrls: ['./africhome-personal-informations.component.scss'],
})
export class AfrichomePersonalInformationsComponent implements OnInit {

  /**
   * Expansion des données pour la partie 1
   */
  public expansionPart1 = false;

  /**
   * Expansion des données pour la partie 2
   */
  public expansionPart2 = false;

  /**
   * Modification des données pour la partie 1
   */
  public modifiedPart1 = false;

  /**
   * Modification des données pour la partie 2
   */
  public modifiedPart2 = false;

  /**
   * Design pour l'entête de la partie 1
   */
  public headerPart1 = true;

  /**
   * Design pour l'entête de la partie 2
   */
  public headerPart2 = true;

  constructor() { }

  ngOnInit() {}

  /**
   * Etent ou non la partie 1
   */
  public expansionChangingPart1() {
    this.expansionPart1 = !this.expansionPart1;
    this.headerPart1 = !this.headerPart1;
  }

  /**
   * Etent ou non la partie 2
   */
  public expansionChangingPart2() {
    this.expansionPart2 = !this.expansionPart2;
    this.headerPart2 = !this.headerPart2;
  }

}
