import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Platform } from '@ionic/angular';
import { AfrichomeConfigProvider } from 'src/providers/africhome-config/africhome-config.provider';

@Component({
  selector: 'africhome-ad',
  templateUrl: './africhome-ad.component.html',
  styleUrls: ['./africhome-ad.component.scss'],
})
export class AfrichomeAdComponent implements OnInit {

  /**
   * L'annonce à afficher
   */
  @Input() public adInformation: {
    _id: string,
    creationDate: any,
    user: {},
    ad: {
      compteType: string,
      adType: string,
      adTitle: string,
      propertyType: string,
      surface: string,
      rooms: number,
      description: string,
      price: {
        value: number,
        devise: string,
      },
      photos:  {
          image1: string,
          image2: string,
          image3: string,
      }
    },
    adress: {
      country: string,
      city: string
    },
    informations: {
      nameOrPseudo: string,
      mail: string,
      number: {
       value: string,
       indicatifNumber: string
      },
      displayNumber: boolean
    }

  };

  @Output() public clickAd = new EventEmitter<any>();

  /**
   * L'image de l'annonce
   */
  public image: string;

  /**
   * La surface en m²
   */
  public surface: string;

  /**
   * Le nombre de pièces
   */
  public roomsNumber: number;

  /**
   * Pour savoir si c'est un professionnel
   */
  public pro: boolean;

  /**
   * Test si la platform est de type web
   */
  public isWeb = false;


  constructor(private config: AfrichomeConfigProvider) { }

  ngOnInit() {
      this.image = this.adInformation.ad.photos.image1;
      this.surface = this.adInformation.ad.surface;
      this.roomsNumber = this.adInformation.ad.rooms;
      this.adInformation.ad.compteType === 'Professionnel' ? this.pro = true : this.pro = false;
      this.isWeb = this.config.isWeb();
  }

 

}
