import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { NavController, AlertController, ModalController } from '@ionic/angular';
import { AfrichomeSearchComponent } from '../africhome-search/africhome-search.component';

@Component({
  selector: 'africhome-header',
  templateUrl: './africhome-header.component.html',
  styleUrls: ['./africhome-header.component.scss'],
})
export class AfrichomeHeaderComponent implements OnInit {

  /**
   * Icon du menu
   */
  @Input() icon: string;

  /**
   * Logo de l'application
   */
  @Input() logo: string;

  /**
   * Activation de l'ombre
   * Si activée = true
   */
  @Input() shadow = true;
  
  /**
   * Event sur le bouton principal du header
   */
  @Output() clickButton = new EventEmitter<string>();

  constructor(private navCtrl: NavController,
              private alertController: AlertController,
              public modalController: ModalController) { }

  ngOnInit() {}

  /**
   * Rédirige l'utilisateur vers l'accueil
   */
  public goHome() {
    this.navCtrl.navigateRoot('/');
  }

 public async presentSearch() {
    const modal = await this.modalController.create({
      component: AfrichomeSearchComponent
    });
    return await modal.present();
  }

  /**
   * Affiche une dialog d'information
   */
  public async search() {

    const alert = await this.alertController.create({
      header: 'ALERT.ALERT_HOME.TITLE',
      cssClass: 'my-custom-alert',
      buttons: [
        {
          text: 'ALERT.ALERT_HOME.YES',
          handler: () => {
            this.navCtrl.navigateForward('/tabs/home');
          }
        },
        {
          text: 'ALERT.ALERT_HOME.NO'
        }
      ]
    });
    await alert.present();
  }

}
