import { Component, OnInit } from '@angular/core';
import { AfrichomeDataProvider } from 'src/providers/africhome-data/africhome-data.provider';
import { AfrichomeControlAdProvider } from 'src/providers/africhome-control-fields/africhome-control-ad.provider';
import { AfrichomeTranslateProvider } from 'src/providers/africhome-translate/africhome-translate.provider';
import { NavController } from '@ionic/angular';

@Component({
  selector: 'africhome-publish-ad',
  templateUrl: './africhome-publish-ad.component.html',
  styleUrls: ['./africhome-publish-ad.component.scss'],
})
export class AfrichomePublishAdComponent implements OnInit {

  /**
   * Liste des pays
   */
  public countries: string[];

  /**
   * Type de compte : professionnel ou personnel
   */
  public typeCompte: string[];

  /**
   * Type de l'annonce : a vendre ou a louer
   */
  public adType: string[];

  /**
   * Type de monnaie
   */
  public devisePrice: string[];

  /**
   * Nombre de pièces
   */
  public numberRooms: string[];

  /**
   * Type de biens
   */
  public propertyType: string[];

  /**
   * Gérer le type de profil
   */
  public isPersonnal: boolean;

  /**
   * Indicatif numéro
   */
  public indicatifNumber: string[];

  public fields = {
    ad : {
      compteType: 'Professionnel',
      adType: 'A louer',
      adTitle: '',
      propertyType: '',
      surface: '',
      rooms: 0,
      description: '',
      price: {
        value: 0,
        devise: '',
      },
      photos:  {
          image1: '',
          image2: '',
          image3: '',
      }
    },
    adress: {
      country: '',
      city: ''
    },
    informations: {
      nameOrPseudo: '',
      mail: '',
      number: {
       value: '',
       indicatifNumber: ''
      },
      displayNumber: ''
    }

  };

  /**
   * Erreur généré
   */
  public errorMsg: string;

  /**
   * Afficher l'erreur ou non
   */
  public display = false;

  constructor(private dataProvider: AfrichomeDataProvider,
              private controlFields: AfrichomeControlAdProvider,
              private translateService: AfrichomeTranslateProvider,
              private navCtrl: NavController) {
  }

  ngOnInit() {
    // type de profile
    this.isPersonnal = false;

    // type de compte
    this.typeCompte = [];
    this.typeCompte.push('COMPONENTS.PUBLISH_AD.YOUR_AD.YOU_ARE.CHOICE.PERSONNAL');
    this.typeCompte.push('COMPONENTS.PUBLISH_AD.YOUR_AD.YOU_ARE.CHOICE.PROFESSIONAL');

    // type d'annonce
    this.adType = [];
    this.adType.push('COMPONENTS.PUBLISH_AD.YOUR_AD.AD_TYPE.CHOICE.BUY');
    this.adType.push('COMPONENTS.PUBLISH_AD.YOUR_AD.AD_TYPE.CHOICE.RENT');

    // type de biens
    this.propertyType = [];
    this.propertyType.push('COMPONENTS.PUBLISH_AD.YOUR_AD.TYPE_OF_PROPERTY.CHOOSE')
    this.propertyType.push('COMPONENTS.PUBLISH_AD.YOUR_AD.TYPE_OF_PROPERTY.HOUSE');
    this.propertyType.push('COMPONENTS.PUBLISH_AD.YOUR_AD.TYPE_OF_PROPERTY.APPART');
    this.propertyType.push('COMPONENTS.PUBLISH_AD.YOUR_AD.TYPE_OF_PROPERTY.LAND');
    this.propertyType.push('COMPONENTS.PUBLISH_AD.YOUR_AD.TYPE_OF_PROPERTY.OFFICE_SHOP');

    // nombre de pièces
    this.numberRooms = [];
    this.numberRooms.push('COMPONENTS.PUBLISH_AD.YOUR_AD.NUMBER_OF_ROOMS.CHOOSE');
    this.numberRooms.push('1');
    this.numberRooms.push('2');
    this.numberRooms.push('3');
    this.numberRooms.push('4');
    this.numberRooms.push('5+');

    // type de monnaie
    // TODO : compléter
    this.devisePrice = [];
    this.devisePrice.push('COMPONENTS.PUBLISH_AD.YOUR_AD.PRICE.CURRENCY');
    this.devisePrice.push('CFA');
    this.devisePrice.push('FGN');

    // liste des pays
    // TODO: traduire en anglais
    this.countries = this.dataProvider.getCountries();

    // indicatif numéro
    // TODO: Compléter en utilisant dataProvider pour renvoyer les données
    this.indicatifNumber = [];
    this.indicatifNumber.push('COMPONENTS.PUBLISH_AD.PERSONNAL_INFORMATIONS.NUMBER.TELEPHONE_CODE');
    this.indicatifNumber.push('+224');
    this.indicatifNumber.push('+244');
    this.indicatifNumber.push('+242');
  }


  /**
   *  Récupere les valeurs des champs
   * @param value
   * @param field1
   * @param field2
   */
  public getFieldsValues(value: any, field1: string, field2: string) {
    this.getFieldsValuesPriceAndNumber(value, field2);
    if ( field2 !== 'price' && field2 !== 'number') {
      this.fields[field1][field2] = value;
    }
    console.log(this.fields)
  }

  /**
   * Traite le formulaire
   */
  public handleForm() {
    const message = this.controlFields.publish(this.fields);
    if (message !== 'valid') {
      this.translateService.getMessage(message).subscribe(
        (translate) => {
          this.errorMsg = translate;
          this.display = true;
        }
      );
    } else {
      this.display = false;
      this.goToPage('/my-ads');
    }

  }

  /**
   * Permet d'être redirigé sur la page de la fonctionnalité sélectionné
   * @param page : string
   */
  public goToPage(page: string) {
    this.navCtrl.navigateForward(page);
  }

  /**
   * Recupére les valeurs des champs : prix et numéro de téléphone
   * @param value
   * @param field2
   */
  private getFieldsValuesPriceAndNumber(value: any, field2: string) {
      if (field2 === 'price') {
        this.fields.ad.price.value = value.inputValue;
        this.fields.ad.price.devise = value.selectValue;
      }
      if (field2 === 'number') {
        this.fields.informations.number.value = value.inputValue;
        this.fields.informations.number.indicatifNumber = value.selectValue;
      }
  }
}
